import { useState, useCallback } from 'preact/compat';
import { ChromeAPI } from '../../shared/chrome-api';
import { ImageUtils } from '../../shared/image-utils';
import type { 
  CaptureResult, 
  ComparisonResult, 
  VRTSettings, 
  HistoryItem,
  ViewType,
  ComparisonTab,
  StepNumber 
} from '../../shared/types';

export function useVRT() {
  // UIÁä∂ÊÖã
  const [currentView, setCurrentView] = useState<ViewType>('main-menu');
  const [currentStep, setCurrentStep] = useState<StepNumber>(1);
  const [activeTab, setActiveTab] = useState<ComparisonTab>('side-by-side');
  const [loading, setLoading] = useState(false);
  const [loadingText, setLoadingText] = useState('Âá¶ÁêÜ‰∏≠...');

  // „Éá„Éº„ÇøÁä∂ÊÖã
  const [beforeImage, setBeforeImage] = useState<CaptureResult | null>(null);
  const [afterImage, setAfterImage] = useState<CaptureResult | null>(null);
  const [comparisonResult, setComparisonResult] = useState<ComparisonResult | null>(null);
  const [settings, setSettings] = useState<VRTSettings>({
    captureDelay: 1,
    imageQuality: 'medium',
    autoScroll: false,
    diffThreshold: 0.1,
    diffColor: '#ff0000'
  });
  const [history, setHistory] = useState<HistoryItem[]>([]);
  const [currentUrl, setCurrentUrl] = useState<string>('');

  // ÂàùÊúüÂåñ
  const initialize = useCallback(async () => {
    try {
      const [loadedSettings, loadedHistory, tabInfo] = await Promise.all([
        ChromeAPI.loadSettings(),
        ChromeAPI.loadHistory(),
        ChromeAPI.getCurrentTab()
      ]);
      
      setSettings(loadedSettings);
      setHistory(loadedHistory);
      setCurrentUrl(tabInfo.url || '');
    } catch (error) {
      console.error('ÂàùÊúüÂåñ„Ç®„É©„Éº:', error);
    }
  }, []);

  // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥
  const navigateTo = useCallback((view: ViewType) => {
    setCurrentView(view);
    
    if (view === 'comparison-view') {
      setCurrentStep(1);
      setBeforeImage(null);
      setAfterImage(null);
      setComparisonResult(null);
      setActiveTab('side-by-side');
      updateCurrentUrl();
    } else if (view === 'history-view') {
      loadHistory();
    }
  }, []);

  // ÁèæÂú®„ÅÆURL„ÇíÊõ¥Êñ∞
  const updateCurrentUrl = useCallback(async () => {
    try {
      const tabInfo = await ChromeAPI.getCurrentTab();
      setCurrentUrl(tabInfo.url || '');
    } catch (error) {
      console.error('URLÂèñÂæó„Ç®„É©„Éº:', error);
    }
  }, []);

  // „Çπ„ÇØ„É™„Éº„É≥„Ç∑„Éß„ÉÉ„ÉàÊíÆÂΩ±
  const captureScreenshot = useCallback(async (type: 'before' | 'after') => {
    setLoading(true);
    setLoadingText(`${type}„Çπ„ÇØ„É™„Éº„É≥„Ç∑„Éß„ÉÉ„Éà„ÇíÊíÆÂΩ±‰∏≠...`);

    try {
      // ÊíÆÂΩ±ÈÅÖÂª∂
      if (settings.captureDelay > 0) {
        await new Promise(resolve => setTimeout(resolve, settings.captureDelay * 1000));
      }

      const result = await ChromeAPI.captureScreenshot({
        imageQuality: settings.imageQuality,
        autoScroll: settings.autoScroll
      });

      if (type === 'before') {
        setBeforeImage(result);
      } else {
        setAfterImage(result);
      }

      return result;
    } catch (error) {
      console.error(`${type}„Çπ„ÇØ„É™„Éº„É≥„Ç∑„Éß„ÉÉ„ÉàÊíÆÂΩ±„Ç®„É©„Éº:`, error);
      throw error;
    } finally {
      setLoading(false);
    }
  }, [settings]);

  // „Çπ„ÉÜ„ÉÉ„ÉóÈÄ≤Ë°å
  const goToStep = useCallback((step: StepNumber) => {
    setCurrentStep(step);
    if (step === 2) {
      updateCurrentUrl();
    }
  }, [updateCurrentUrl]);

  // ÊØîËºÉÂÆüË°å
  const compareImages = useCallback(async () => {
    if (!beforeImage || !afterImage) {
      throw new Error('Before/AfterÁîªÂÉè„Åå‰∏çË∂≥„Åó„Å¶„ÅÑ„Åæ„Åô');
    }

    setLoading(true);
    setLoadingText('ÁîªÂÉè„ÇíÊØîËºÉ‰∏≠...');

    try {
      const result = await ImageUtils.compareImagesAdvanced(beforeImage, afterImage, {
        threshold: settings.diffThreshold,
        diffColor: settings.diffColor,
        includeAA: false,
        alpha: 0.1
      });

      setComparisonResult(result);
      setCurrentStep(3);

      // Â±•Ê≠¥„Å´‰øùÂ≠ò
      await ChromeAPI.saveToHistory({
        timestamp: result.timestamp,
        beforeUrl: result.beforeUrl,
        afterUrl: result.afterUrl,
        diffPercentage: result.diffPercentage,
        diffPixels: result.diffPixels,
        beforeImageData: beforeImage.dataUrl,
        afterImageData: afterImage.dataUrl,
        diffImageUrl: result.diffImageUrl
      });

      return result;
    } catch (error) {
      console.error('ÁîªÂÉèÊØîËºÉ„Ç®„É©„Éº:', error);
      throw error;
    } finally {
      setLoading(false);
    }
  }, [beforeImage, afterImage, settings]);

  // ÁµêÊûú‰øùÂ≠ò
  const saveResult = useCallback(async () => {
    if (!comparisonResult || !beforeImage || !afterImage) return;

    try {
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
      const reportHtml = ImageUtils.generateReportHtml(comparisonResult, beforeImage, afterImage);
      const blob = new Blob([reportHtml], { type: 'text/html' });
      
      await ChromeAPI.downloadFile(blob, `vrt-report-${timestamp}.html`);
      await ChromeAPI.showNotification('Quick VRT', '„É¨„Éù„Éº„Éà„Çí‰øùÂ≠ò„Åó„Åæ„Åó„Åü');
    } catch (error) {
      console.error('‰øùÂ≠ò„Ç®„É©„Éº:', error);
      throw error;
    }
  }, [comparisonResult, beforeImage, afterImage]);

  // Ë®≠ÂÆö‰øùÂ≠ò
  const saveSettings = useCallback(async (newSettings: VRTSettings) => {
    try {
      await ChromeAPI.saveSettings(newSettings);
      setSettings(newSettings);
      await ChromeAPI.showNotification('Quick VRT', 'Ë®≠ÂÆö„Çí‰øùÂ≠ò„Åó„Åæ„Åó„Åü');
    } catch (error) {
      console.error('Ë®≠ÂÆö‰øùÂ≠ò„Ç®„É©„Éº:', error);
      throw error;
    }
  }, []);

  // Â±•Ê≠¥Ë™≠„ÅøËæº„Åø
  const loadHistory = useCallback(async () => {
    try {
      const loadedHistory = await ChromeAPI.loadHistory();
      setHistory(loadedHistory);
    } catch (error) {
      console.error('Â±•Ê≠¥Ë™≠„ÅøËæº„Åø„Ç®„É©„Éº:', error);
    }
  }, []);

  // Êñ∞„Åó„ÅÑÊØîËºÉÈñãÂßã
  const startNewComparison = useCallback(() => {
    navigateTo('comparison-view');
  }, [navigateTo]);

  return {
    // UIÁä∂ÊÖã
    currentView,
    currentStep,
    activeTab,
    loading,
    loadingText,
    
    // „Éá„Éº„Çø
    beforeImage,
    afterImage,
    comparisonResult,
    settings,
    history,
    currentUrl,
    
    // „Ç¢„ÇØ„Ç∑„Éß„É≥
    initialize,
    navigateTo,
    updateCurrentUrl,
    captureScreenshot,
    goToStep,
    compareImages,
    saveResult,
    saveSettings,
    loadHistory,
    startNewComparison,
    setActiveTab
  };
}